<script>
  import Slider from "./Slider.svelte";
  let redColor = 120;
  let blueColor = 0;
  let greenColor = 0;
  
  let showError = false;
  let errorMessage = "";
  
  let writer;
  
  function updateRed(e) {
    redColor = e.detail;
  }
  function updateBlue(e) {
    blueColor = e.detail;
  }
  function updateGreen(e) {
    greenColor = e.detail;
  }

  async function connectUSB() {
    showError = false;
    try {
      const port = await navigator.serial.requestPort();

      await port.open({
        baudrate: 115200
      });
      writer = port.writable.getWriter();
    } catch (e) {
      showError = true;
      console.error(e);
      errorMessage = e.message;
    }
  }

  $: colorCss = `rgb(${redColor}, ${greenColor}, ${blueColor})`;

  $: arduinoColor = `${redColor}:${greenColor}:${blueColor}|`;

  $: if (writer && arduinoColor) {
    const enc = new TextEncoder(); // always utf-8
    writer.write(enc.encode(arduinoColor));
  }
</script>

<style>
  main {
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  #color_picker {
    width: 100%;
    height: 300px;
    border: solid black 2px;
  }
  button {
    margin: 20px 0;
  }
</style>

<main>
  <h1>Arduino Color Picker</h1>
  <h2>
    <a href="https://github.com/phptuts/arduino-svelte-color-picker">
      Click here for setup instructions
    </a>
  </h2>
  <p>
    Besure experimental webplatorm features are enabled if you are using a
    version of chrome under 80. Do this by going to this url in the browser:
    chrome://flags.
  </p>
  <button on:click={connectUSB}>Connect To Arduino</button>
  <br />
  <Slider
    min="0"
    max="0"
    colorNumber="50"
    sliderColor="#AA0000"
    on:color={updateRed} />

  <Slider
    min="0"
    max="0"
    colorNumber="0"
    sliderColor="#00AA00"
    on:color={updateGreen} />

  <Slider
    min="0"
    max="0"
    colorNumber="0"
    sliderColor="#0000AA"
    on:color={updateBlue} />

  <section style="background: {colorCss}" id="color_picker" />
  {#if showError}
    <section>
      <h2>Error Message</h2>
      <pre>{errorMessage}</pre>
    </section>
  {/if}
  <h2>Circuit Picture</h2>
  <img src="rgb_led.svg" alt="picture of circuite" />

</main>
